'use client';

import { useState } from 'react';
import Link from 'next/link';

export default function TrackJourneyPage() {
    const [currentStatus, setCurrentStatus] = useState('picked-up');

    return (
        <div className="min-h-screen bg-black text-white flex flex-col">
            {/* Status Bar */}
            <div className="flex justify-between items-center p-4 pt-12 text-sm">
                <span>9:41</span>
                <div className="flex items-center gap-1">
                    <div className="flex gap-1">
                        <div className="w-1 h-1 bg-white rounded-full"></div>
                        <div className="w-1 h-1 bg-white rounded-full"></div>
                        <div className="w-1 h-1 bg-white rounded-full"></div>
                        <div className="w-1 h-1 bg-white rounded-full"></div>
                    </div>
                    <svg className="w-4 h-4" viewBox="0 0 24 24" fill="white">
                        <path d="M2 17h20v2H2v-2zm1.15-6.05L4 9.47l.85 1.48H5c.55 0 1 .45 1 1s-.45 1-1 1-.45-.45-.45-1zm5.5 2.5H9c.55 0 1-.45 1-1s-.45-1-1-1-.45.45-.45 1z" />
                    </svg>
                    <div className="w-6 h-3 border border-white rounded-sm">
                        <div className="w-4 h-1.5 bg-white rounded-sm m-0.5"></div>
                    </div>
                    <svg className="w-4 h-4" viewBox="0 0 24 24" fill="white">
                        <path d="M12 2C13.1 2 14 2.9 14 4C14 5.1 13.1 6 12 6C10.9 6 10 5.1 10 4C10 2.9 10.9 2 12 2Z" />
                    </svg>
                </div>
            </div>

            {/* Header */}
            <div className="text-center px-8 mb-8">
                <h1 className="text-4xl font-bold mb-2">Pickom</h1>
                <p className="text-gray-400 text-lg">People-Powered Delivery</p>

                <h2 className="text-3xl font-bold mt-12 mb-8">Track Journey</h2>
            </div>

            {/* Journey Visualization */}
            <div className="flex-1 px-8 relative">
                {/* Route */}
                <div className="flex flex-col items-center space-y-8">
                    {/* Start Point - Warsaw */}
                    <div className="flex items-center justify-center">
                        <div className="w-12 h-12 bg-gray-600 rounded-full flex items-center justify-center">
                            <svg className="w-8 h-8 text-gray-300" viewBox="0 0 24 24" fill="currentColor">
                                <path d="M12 2C13.1 2 14 2.9 14 4C14 5.1 13.1 6 12 6C10.9 6 10 5.1 10 4C10 2.9 10.9 2 12 2Z" />
                            </svg>
                        </div>
                        <span className="ml-4 text-lg font-medium">Warsaw</span>
                    </div>

                    {/* Path Line */}
                    <div className="relative">
                        <svg width="2" height="200" className="absolute left-1/2 transform -translate-x-1/2">
                            <path
                                d="M1 0 Q50 50 1 100 Q-50 150 1 200"
                                stroke="white"
                                strokeWidth="2"
                                fill="none"
                                strokeDasharray="5,5"
                                className="animate-pulse"
                            />
                        </svg>
                    </div>

                    {/* Current Position - Kraków */}
                    <div className="flex items-center justify-center relative">
                        <div className="w-12 h-12 bg-orange-500 rounded-full flex items-center justify-center animate-pulse">
                            <div className="w-8 h-8 bg-gray-600 rounded-full flex items-center justify-center">
                                <svg className="w-6 h-6 text-gray-300" viewBox="0 0 24 24" fill="currentColor">
                                    <path d="M12 2C13.1 2 14 2.9 14 4C14 5.1 13.1 6 12 6C10.9 6 10 5.1 10 4C10 2.9 10.9 2 12 2Z" />
                                </svg>
                            </div>
                        </div>
                        <span className="ml-4 text-lg font-medium">Kraków</span>
                    </div>
                </div>

                {/* Estimated Arrival */}
                <div className="text-center mt-12">
                    <h3 className="text-xl font-bold mb-2">Estimated Arrival</h3>
                    <p className="text-2xl text-orange-500 font-bold">Today at 4:25 PM</p>
                </div>
            </div>

            {/* Track Package Button */}
            <div className="p-8">
                <Link
                    href="/delivery-confirmation"
                    className="w-full bg-transparent border-2 border-white text-white py-4 rounded-full text-lg font-medium text-center block hover:bg-white hover:text-black transition-colors"
                >
                    Track Package
                </Link>
            </div>

            {/* Bottom Indicator */}
            <div className="flex justify-center pb-6">
                <div className="w-32 h-1 bg-white rounded-full"></div>
            </div>
        </div>
    );
} 